import React,{useState, useEffect , useRef} from 'react'
import {useGlopalContext} from './Context'
function Submenu() {
  const {isSubmenuOpen ,location ,pages:{page,links} } = useGlopalContext()
  const container = useRef(null)
  const [colimin , setColimin] = useState('col-2')
  useEffect(() => {
   setColimin('col-2')
  const submenu = container.current;
  const {center , bottom} = location;
   submenu.style.left = `${center}px`;
   submenu.style.top = `${bottom}px`;
   if(links.length === 3){
    setColimin('col-3')
   }
   if(links.length > 3){
    setColimin('col-4')
   }
  
  }, [location , links] )
 return (
  <div className={`${isSubmenuOpen ? 'sub-menu submenu-active':'sub-menu'}`} ref={container}>
   <h4>{page}</h4>
   <div className={`submenu-links ${colimin}`}>
    {
     links.map((link)=>{
      const {label, icon,url} = link
      return <a href={url}> {icon}  {label}</a>
     })
    }
   </div>
  </div>
 )
}

export default Submenu
