import React from 'react'
import phone from './Images/phone.svg';
import {useGlopalContext} from './Context'
function Hero() {
   const {setIsSubmenuOpen } = useGlopalContext()
 return <>
 <div className="hero-container" onMouseOver={()=>setIsSubmenuOpen(false)}>
  <article className='content-article'>
    <div className="details-container">
     <h1>Payments infrastructure <br></br> for the internet</h1>
    <p>Millions of companies of all sizes—from startups to Fortune 500s—use Stripe’s software and APIs to accept payments, send payouts, and manage their businesses online.</p>
    <br/>
    <button className="btn start-btn"> start now</button>
    </div>
    <div className="img-container">
     <img src={phone} alt="Logo"/>
    </div>
    
   </article>
 </div>
 </>
}

export default Hero
