import React from 'react'
import { FaTimes } from "react-icons/fa";
import {useGlopalContext} from './Context'
import sublinks from './data'

function Sidebar() {
 const {isSidebarOpen,setIsSidebarOpen} = useGlopalContext()
 return (
  <div className={`${isSidebarOpen? 'sidebar-container sidebar-active': 'sidebar-container'}`}>
   <div className="sidebar-content">
    <button className='btn sidebar-btn' onClick ={()=>setIsSidebarOpen(false)}><FaTimes/></button>
    <div className="side-links"></div>
    {
     sublinks.map((link , index)=>{
      const {page , links} = link
      return <article key={index}>
       <h4>{page}</h4>
       <div className="sidebar-sublinks">
        {
         links.map((single , index)=>{
          const {label , icon , url} = single
          return <a href={url}  key = {index}> {icon} {label}</a>
         })
        }
       </div>
      </article>
     })
    }
   </div>
  </div>
 )
}

export default Sidebar
