import React from 'react'
import { FaBars } from "react-icons/fa";
import {useGlopalContext} from './Context'
function Navbar() {
 const {setIsSidebarOpen,OpenFunction} = useGlopalContext()

 const ShowSubMenu = (e)=>{
  const page = e.target.textContent;
  const tempBtn = e.target.getBoundingClientRect();
  const center = (tempBtn.left + tempBtn.right)/2;
  const bottom  = tempBtn.bottom+10;
  console.log(tempBtn)
  OpenFunction(page,{center,bottom})
 }
 return (
  <nav>
   <div className="logo-container">
    <img src="https://react-projects-13-stripe-submenus.netlify.app/static/media/logo.1090473d.svg" alt="strip logo"/>
   </div>
   <div className="links-container">
    <ul className='nav-links'>
     <li><button className='btn'  onMouseOver = {ShowSubMenu}>products</button></li>
     <li><button className='btn'  onMouseOver = {ShowSubMenu}>developers</button></li>
     <li><button className='btn'  onMouseOver = {ShowSubMenu}>company</button></li>
    </ul>
   </div>
   <button className='btn sign-btn'>Sign In</button>
   <button className='btn sidebar-btn' onClick = {()=> setIsSidebarOpen(true)}><FaBars/></button>

  </nav>
 )
}

export default Navbar
