import React , { useContext , useState} from 'react'
import sublinks from './data'
const AppContext = React.createContext()

const AppProvider = ({children})=>{

 const [isSidebarOpen , setIsSidebarOpen] = useState(false)
 const [isSubmenuOpen , setIsSubmenuOpen] = useState(false)
 const [location , setLocation] = useState({});
 const [pages , setPages] = useState({page:'' , links:[]})

 const OpenFunction =(text , cordiants)=>{
  const page = sublinks.find((link)=> link.page === text)
  setPages(page)
  setLocation(cordiants)
  setIsSubmenuOpen(true)
 }

 return <AppContext.Provider value = {{isSidebarOpen,setIsSidebarOpen,isSubmenuOpen,setIsSubmenuOpen , OpenFunction ,location ,pages}}>
  {children}
 </AppContext.Provider>
}

export const useGlopalContext = ()=>{
 return useContext(AppContext);
}

export {AppContext , AppProvider}